.container_header {
	width: 100%;
	height: 72px;
	padding: 20px 0px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	z-index: 1;
}
.container_header::before {
	content: '';
	position: absolute;
	background-image: url('../../img/Vector (23).png');
	background-repeat: no-repeat;
	background-size: 100% 100%;
	width: 200px;
	height: 110px;
	z-index: 0;
}
.content_logo {
	position: relative;
	z-index: 2;
}
.content_a {
	font-family: 'Trebuchet MS';
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 20px;
	text-align: center;
	color: #333333;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 320px;
	max-width: 100%;
}
.content_lang {
	display: flex;
	flex-direction: row;
}
.content_lang_logo {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}
.name_lang {
	margin-right: 8px;
}
.prapor {
	margin-right: 16px;
}
@media (max-width: 900px) {
	.content_a {
		display: none;
	}
}
@media (max-width: 900px) {
	.content_lang_logo {
		display: flex;
		flex-direction: column-reverse;
		justify-content: space-between;
		align-items: center;
	}
}
@media (max-width: 480px) {
	.container_header::before {
		left: 25%;
		width: 200px;
		height: 70px;
		z-index: 0;
	}
	.content_lang_logo {
		display: flex;
		flex-direction: column-reverse;
		justify-content: space-between;
		align-items: flex-start;
	}
	.content_logo {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.container_header {
		padding: 10px 0px;
		padding-bottom: 0px;
		width: 100%;
		height: 156px;
		display: flex;
		flex-direction: column;
		align-items: stretch;
		z-index: 1;
	}
}
