.container_navigation_menu {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding-top: 40px;
	margin-bottom: 0px;
	margin-right: 20px;
}
.children_content {
	width: 838px;
	max-width: 100%;
	background: #ffffff;
	box-shadow: 0px 16px 25px rgba(0, 0, 0, 0.16);
	border-radius: 24px;
	padding: 64px;
	padding-bottom: 10px;
	margin-bottom: 20px;
}
@media (max-width: 940px) {
	.container_navigation_menu {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-top: 40px;
		margin-bottom: 0px;
		margin-right: 0px;
	}
	.children_content {
		padding: 5px;
	}
}
@media (max-width: 480px) {
	.container_navigation_menu {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-top: 10px;
		margin-bottom: 0px;
		margin-right: 0px;
	}
}
