.scroll_navigation {
	overflow-y: scroll;
	height: 500px;
	padding-right: 10px;
	padding-top: 10px;
	margin-top: 20px;
	padding-bottom: 100px;
}

.scroll_navigation::-webkit-scrollbar {
	width: 7px;
	background-color: #ffffff;
}
.scroll_navigation::-webkit-scrollbar-thumb {
	background: #eeeeee;
	border-radius: 4px;
}
.div_none_scroll {
	height: 100vh;
	overflow: hidden;
}
@media (max-width: 500px) {
	.scroll_navigation {
		height: 30vh;
	}
}
