.content_down {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}
.margin_ {
	margin-left: 64px;
	width: 100%;
}
@media (max-width: 900px) {
	.content_down {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.margin_ {
		margin-left: 0px;
	}
}
