.content_user {
	font-family: Trebuchet MS;
	font-style: normal;
	font-weight: bold;
	font-size: 24px;
	line-height: 32px;
	display: flex;
	align-items: center;
	color: #333333;
	padding: 8px 16px;
	margin-bottom: 16px;
}
.user_img {
	margin-right: 16px;
}
.container_Menu {
	padding-top: 40px;
}
.burger {
	display: none;
}
.container_user {
	display: flex;
	flex-direction: row;
	align-items: center;
}
@media (max-width: 940px) {
	.container_user {
		display: flex;
		flex-direction: row;
		align-items: center;
	}
	.content_user {
		justify-content: space-between;
	}
	.burger {
		display: flex;
		flex-direction: column;
		margin: 10px 0px;
	}
	.burger div {
		width: 20px;
		height: 3px;
		background-color: #705ed8;
		margin: 2px 0px;
	}
	.menu_Mob {
		width: 0px;
		font-size: 0px;
		position: absolute;
		overflow-x: hidden;
		background-color: #ffffff;
		z-index: 4;
	}
	.mob_menu_active {
		width: 100%;
		padding-right: 10px;
		height: auto;
		position: absolute;
		background-color: #ffffff;
		z-index: 101;
		padding-bottom: 100px;
	}
	.exite {
		display: flex;
		flex-direction: column;
	}
	.exite div {
		width: 24px;
		height: 2px;
		background-color: #705ed8;
		margin: 2px 0px;
		transform: rotate(47deg);
	}
	.exite .pointer {
		width: 24px;
		height: 2px;
		background-color: #705ed8;
		margin: 2px 0px;
		transform: rotate(127deg);
		position: relative;
		bottom: 5px;
	}
}
