.App {
	max-width: 1320px;
	margin: 0 auto;
	padding-left: 60px;
	padding-right: 60px;
}
.content_navigatino {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-top: 16px;
}
.content_btn_setting {
	display: flex;
	flex-direction: row;
	justify-content: center;
}
a {
	text-decoration: none;
}
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap');
/* font-family: 'Ubuntu', sans-serif; */
@font-face {
	font-family: 'Trebuchet MS';
	src: local('Trebuchet MS'),
		url(../src/component/fonts/trebuc.ttf) format('trebuc.ttf');
}
@media (max-width: 900px) {
	.App {
		padding-left: 10px;
		padding-right: 10px;
	}
}
